                    width: 2.5em;
                }
            } */
        }
    }
}

.container1 {
    max-width: 1200px;
    width: 100vw;
    margin: 0 auto;
    padding: 2rem;

    .position-grid {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-gap: 15%;

        .text-position {
            grid-column: 1 / 2;
            font-size: 14px;

            h2 {
                font-size: 40px;
                font-size: 2.5rem;
                margin-bottom: 1.5rem;
            }
        }
    }

    .svg-container {
        display: flex;
        flex-direction: column;
        align-content: center;
        justify-content: space-around;

        .bedroom-svg {
            width: 100%;
            max-width: 65%;
            grid-column: 2 / 3;
            margin: 0 auto;
        }
        .xp-svg {
            width: 100%;
            max-width: 45%;
            grid-column: 2 / 3;
            margin: 0 auto;
        }
    }
}

.button-alignment {
    margin-top: 5rem;
}

.previous {
    grid-column: 1 / 2;
    padding: 0.8rem 4rem;
    color: black;
    width: 125px;
    white-space: nowrap;
    justify-self: end;
    background-color: #f5f5f5;
    outline: none;
    font-family: aktiv-grotesk, sans-serif;
    border: none;
    cursor: pointer;
    font-size: 16px;
}
.previous:hover {
    opacity: 50%;
}

.next {
    justify-self: end;
    grid-column: 2 / 3;
    padding: 0.8rem 4rem;
    color: white;
    width: 125px;
    white-space: nowrap;
    font-size: 16px;
    justify-self: start;
    background-color: black;
    outline: none;
    font-family: aktiv-grotesk, sans-serif;
    border: none;
    text-align: center;
    cursor: pointer;
}
.next:hover {
    opacity: 50%;
}
.greenBtn {
    background-color: #7df283;
    color: black;
}
.center {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 15%;
    margin-bottom: 5rem;
}
.qualification-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 10%;

    .grid-column-1 {
        grid-column: 1 / 2;
    }
    .grid-column-2 {
        grid-column: 2 / 3;
    }

    .stacks-box {
        font-size: 14px;
        background-color: #f0eeed;
        padding: 1rem 2.5rem;
        height: max-content;
        ul {
            list-style-type: square;
            padding-left: 1rem;

            li {
                padding: 0.7rem 0;
                font-size: 14px;
                font-size: 0.875rem;
                line-height: 1.6;
            }
        }
        h3 {
            font-size: 20px;
            font-size: 1.25rem;
            margin-bottom: 1.5rem;
            font-weight: 700;
        }
    }
}

.svg-container {
    display: flex;
    flex-direction: column;
    align-content: center;
    justify-content: space-around;
    grid-row-gap: 3rem;

    .bedroom-svg {
        width: 100%;
        max-width: 65%;
        grid-column: 2 / 3;
        margin: 0 auto;
    }
    .xp-svg {
        width: 100%;
        max-width: 45%;
        grid-column: 2 / 3;
        margin: 0 auto;
    }
}

.conversations {
    display: flex;
    flex-direction: column;
    align-content: center;

    div {
        display: flex;
        grid-gap: 30%;
    }
    .coffee-svg {
        width: 100%;
        max-width: 25%;
        grid-column: 2 / 3;
        margin: 0 auto;
    }
    .pair-svg {
        width: 100%;
        max-width: 25%;
        grid-column: 2 / 3;
        margin: 0 auto;
    }
    .convos-svg {
        width: 100%;
        max-width: 75%;
    }

    hr {
        width: 100%;
        background-color: black;
        border: 1px solid black;
    }
    a {
        font-size: 2.2rem;
        margin: 1rem 0;
    }

    h4 {
        margin: 2rem 0;
    }
}

.contactperson-container {
    display: grid;
    grid-template-columns: 1fr 1fr;
    background-color: #f0eeed;
    padding: 1.5rem;
    margin-top: 2rem;

    img {
        grid-column: 1 / 2;
        width: 90%;
    }

    div {
        grid-column: 2 / 3;
        height: max-content;

        h3 {
            margin-bottom: 0.5rem;
            margin-top: 0;
        }
        p {
            line-height: 1.2;
            margin: 0;
        }
    }
}

.convo-text p {
    font-size: 0.8rem;
    text-align: center;
    margin-bottom: 4rem;
}

//MEDIA QUERIES

@media screen and (max-width: 600px) {
    .heading {
        grid-template-columns: 1fr;

        h1 {
            margin: 0;
        }
    }

    .bigHeader {
        width: 90vw;

        img {
            max-width: 60%;
        }
    }

    ol.progress-track {
        margin: 0 auto;
        padding: 0 2rem 0 0;
        width: 90%;
    }

    ol.progress-track li .progress-text {
        right: 0;
    }
    .phone {
        right: 1.5rem !important;
    }

    ol.progress-track li .progress-text2 {
        right: 1rem;
    }
    ol.progress-track li .progress-text3 {
        margin-left: -0.3rem;
    }

    .container1 .position-grid {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr auto;
        grid-gap: 0;
    }
    .container1 .svg-container {
        flex-direction: row;
    }

    .bedroom-svg {
        grid-row: 2 / 3;
        max-width: 100% !important;
    }
    .xp-svg {
        display: none !important;
    }

    .center {
        display: flex;
        flex-direction: column-reverse;
        align-items: center;
        margin-top: 1rem;
    }
    .next {
        margin-bottom: 2rem;
    }

    .qualification-grid {
        grid-template-columns: 1fr;
        grid-gap: 1%;
    }
    .qualification-grid .stacks-box {
        padding: 0.5rem 2rem;
    }
    .qualification-grid .grid-column-2 {
        grid-column: 1 / 2;
    }
    .contactperson-container {
        grid-template-columns: 1fr;
        grid-template-rows: 1fr auto;
        width: fit-content;
        padding: 2rem 1rem;
        margin: 0;
        grid-gap: 1rem;

        div {
            grid-column: 1/2;
            width: fit-content;
            margin: 0 auto;
        }
        img {
            width: 100%;
            max-width: 75%;
            margin: 0 auto;
        }
    }
    .phone-none-grid {
        display: flex !important;
        flex-direction: column;
    }
    .conversations a {
        font-size: 1.5rem;
    }
}
